@mixin margin-start($value) {
    -webkit-margin-start: $value;
    -moz-margin-start: $value;
    margin-inline-start: $value;
}
@mixin transition {
    transition: all 0.35s ease-in-out;
}  
@mixin transition-img {
    transition: transform 8s cubic-bezier(0.25, 0.46, 0.45, 0.94), -webkit-transform 8s cubic-bezier(0.25, 0.46, 0.45, 0.94);       
}
.section-featured-categories-list {
    .category {
        padding: var(--gutter-xs);
        background-color: var(--second-bg-color);  
        @include transition; 
        >a {
            display: block;
        }
        .title {
            font-size: calc(var(--body-font-size) + 2px); 
            padding-top: var(--gutter-xs); 
            color: var(--body-link);
            font-weight: var(--font-body-weight);
            &:hover {
                @media (hover: hover) {
                    color: var(--body-link-hover); 
                }
            }
        }
        &:hover {
            @media (hover: hover) {
                box-shadow: 0px 5px 24px rgba(35, 35, 35, 0.18); 
            } 
        }
    }
    .section-banner { 
        overflow: hidden;
        .image__style {
            height: 100%;
        }
        img {
            @include transition-img;
        }
        .heading {
            margin-bottom: 10px; 
        }
        .subtext {
            margin-bottom: 20px;
            p {
                margin: 0;
                &:not(:last-child) {
                    margin-bottom: 5px; 
                }
            }
        }
        .btn {
            display: inline-block;
            width: 100%;
            padding: 11px 10px;
            @media (min-width: 576px) {
                width: auto;
                padding-left: 30px;
                padding-right: 30px;
            }
        }
        .text-center .btn {
            margin: 0 auto;
        }
        .text-right .btn {
            @include margin-start(auto); 
        }
        &:hover {
            @media (hover: hover) {
                img {
                    transform: scale(1.2); 
                }
            }
        }
    }
}